.imgWidth {
  width: 100%;
  height: auto;
}
.homeBody {
  margin-right: 0px;
  margin-left: 0px;
  margin-top: 40px;
}
@media (min-width: 1280px) {
  .homeBody {
    margin-right: 50px !important;
    margin-left: 70px;
  }
}
@media (min-width: 2200px) {
  .homeBody {
    margin-left: 25px;
  }
}
@media (max-width: 900px) {
  .bottomCentre {
    position: absolute;
    width: 100%;
    margin-top: -19px;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 13px;
    color: #000000;
    padding: 10px 0 10px 0;
    background-color: rgba(255, 255, 255, 0.6);
    letter-spacing: 1px;
  }
}
@media (min-width: 900px) {
  .bottomCentre {
    position: absolute;
    width: 100%;
    margin-top: -38px;
    left: 50%;
