.showme {
  display: none;
}
.showhim:hover .showme {
  display: block;
}
.showhim:hover .hideme {
  display: none;
  -webkit-animation: seconds;
  -webkit-animation-iteration-count: 1;
  -webkit-animation-duration: 2s;
  animation: seconds;
  animation-iteration-count: 1;
  animation-duration: 2s;
  position: relative;
}
@-webkit-keyframes seconds {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    left: -9999px;
    position: absolute;
  }
}
@keyframes seconds {
  0% {
    opacity: 1;
